import axios from "axios";

const url = "https://optimizators.herokuapp.com";
//const url = "http://localhost:2500";

export default {
  updateUser({ commit }) {
    commit("updateUser");
  },
  async login({ commit }, user) {
    await axios(url + "/auth/login", {
      method: "post",
      data: user,
    });
    commit("updateUser");
  },
  async logout({ commit }) {
    await axios(url + "/auth/logout", {
      method: "post",
    });
    commit("updateUser");
  },
  async register({ commit }, user) {
    try {
      await axios(url + "/auth/register", {
        method: "post",
        data: user,
      });
    } catch (ex) {
      console.log(ex.response);
      console.log(ex);
    }
    commit("updateUser");
  },
};
