export default {
  isAuthenticated: (state) => {
    return state.user != null;
  },

  getUserId: (state) => {
    return state.userId;
  },

  getEmail: (state) => {
    return state.user.email;
  },

  getToken: (state) => {
    return state.token;
  },
  getRole: (state) => {
    return state.userRole;
  },
};
