export default {
  updateUser(state) {
    try {
      let authToken = document.cookie.split(";")[0];
      if (authToken.split("=")[0] == "Authorization") {
        let payload = JSON.parse(atob(authToken.split("=")[1].split(".")[1]));
        state.user = payload.user;
      } else {
        state.user = null;
      }
    } catch {
      state.user = null;
    }
  },
};
