<template>
  <div class="header">
    <template  v-if="isAuthenticated">
      <button class="right" @click="logOut()">Выйти</button>
      <router-link class="right" to="/profile"><a>{{user.login}}</a></router-link>
    </template>
    <template v-else>
      <router-link class="right" to="/login"><a>Вход</a></router-link>
      <router-link class="right" to="/register"><a>Регистрация</a></router-link>   
    </template>  
    <router-link class="right" to="/"><a>Главная</a></router-link>    
  </div>
  <router-view/>
</template>

<script>
import { mapActions} from 'vuex'

export default {
  computed:
  {
    isAuthenticated() {
      return this.user != null;
    },
    user() { return this.$store.state.auth.user},
  },
  methods:{
    ...mapActions('auth',['updateUser']),
    ...mapActions('auth',['logout']),
    async logOut()
    {
      await this.logout();
      this.$router.push('/login')
    }
  },
  beforeMount()
  {
    this.updateUser();
  }
}
</script>


<style>
.header>a{
  color: white;
  text-decoration: none;
}
.right {
  display: block; 
  text-align: center;
  padding: 14px 20px;  
  float: right;
}
.header {
  overflow: hidden;
  background-image: linear-gradient(45deg, #6ab1d7 0%, #33d9de 50%, #002878 100%);
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  box-shadow: 0 16px 32px 0 rgba(0, 40, 120, .35);
}
.footer{
  overflow: hidden;
  bottom: 0;
  position: fixed;
  background-image: linear-gradient(45deg, #6ab1d7 0%, #33d9de 50%, #002878 100%);
  width: 100%;
  color: white;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
body{
  margin: 0;
}
.textboxes{
    box-sizing: border-box;
    box-shadow: inset 0 1px 5px rgba(0,0,0,0.2);
    border: 1px solid #ccc;
    width: 400px;
    height: 50px;
    font-size: 24px;
}
.buttons{
  border-radius: 5px;
  text-decoration: none;
  display: inline-block;
  padding: 10px 40px;
  background-image: linear-gradient(45deg, #6ab1d7 0%, #33d9de 50%, #002878 100%);
  background-position: 100% 0;
  background-size: 200% 200%;
  font-size: 24px;
  font-weight: 300;
  color: white;
  box-shadow: 0 16px 32px 0 rgba(0, 40, 120, .35);
  transition: .5s;
}
.buttons:hover{
  box-shadow: 0 0 0 0 rgba(0, 40, 120, 0);
  background-position: 0 0;
}
</style>