export default {
    addRefTable: (state, row) => {
        state.ref_table_1.push(row)
        localStorage.setItem('RT1', JSON.stringify(state.ref_table_1))
        //console.log(state.teams)
    },
    clearRefTable:(state)=>{
        state.ref_table_1.splice(0)
        localStorage.removeItem('RT1')
    }
}